.App {
  width: 70%;
  margin: 50px auto;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.logo {
  font-size: 32px;
  font-weight: 700;
  color: $red;
  text-decoration: none;
}

.container {
  display: flex;
  min-width: 750px;
  height: calc(100vh - 165px);
  border: 1px solid $white;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px #0000001A;
}

.text, input, input::placeholder {
  font-family: $base-font;
  font-size: 14px;
  font-weight: 400;
  color: $text-grey;
}